@import './utils/mixins.scss';

html {
  margin: 0;
}

body {
  margin: 0;
}

.header {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-image: url(../images/law.jpg);

  &__nav {
    display: flex;
    justify-content: space-between;
  }

  &__menu {
    position: fixed;
    margin-right: 60px;
    margin-top: 5px;
    margin-left: 20px;

    @include menu;
  }

  &__block {
    @include menu;
  }

  &__logo {
    display: block;
    content: "";
    background-image: url(../images/logo-transparent-svg.svg);
    height: 100px;
    width: 100px;
    
    @include onTablet {
      height: 150px;
      width: 150px;
    }
  }

  &__icons {
    margin-top: 20px;
    margin-right: 20px;
    text-decoration: none;
  }

  &__icon:not(:last-child) {
    margin-right: 10px;
    color: transparent;
  }

  &__title {
    color: #fff;
    align-self: center;
    margin-left: 0;
    text-align: center;
    font-size: 50px;
    font-family: Manrope;
    font-weight: bold;
    
    margin-top: 50vh;
  }

  &__arrow {
    background-image: url(../images/white-down-arrow-png-2.png);
    height: 30px;
    width: 30px;
  }
}

.icon {
  height: 24px;
  width: 24px;
}