@mixin onTablet {
  @media (min-width: 550px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1200px) {
    @content;
  }
}

@mixin menu {
  height: 30px;
    width: 30px;

    @include onTablet {
      height: 44px;
      width: 44px;
      margin-right: 66px;
    }
    
    @include onDesktop {
      height: 50px;
      width: 50px;
      margin-right: 50px;
    }
}